import React from 'react';
import styles from './button.css';
import classnames from 'classnames';

type Props = {
  secondary?: boolean;
  children: any;
  href: string;
};

export default function Button({ secondary, children, href }: Props) {
  return (
    <a
      href={href}
      className={classnames(styles.button, secondary && styles.secondary)}
    >
      {children}
    </a>
  );
}
